.productsPage {
  max-width: 1000px;
  margin: 30px auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.pageHead {
  text-align: center;
  margin-bottom: 25px;
  font-size: 26px;
}

.emptyText {
  text-align: center;
  color: #666;
  font-size: 16px;
}

.productGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 20px;
}
