.signUpPage {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 90vh;
  padding: 2rem;
  margin: 10vh;
}

.signUpCard {
  background-color: #1E90FF;
  padding: 3rem;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  width: 100%;
  max-width: 450px;
  text-align: center;
}

.pageHead {
  font-family: 'Inter', sans-serif;
  font-weight: 600;
  font-size: 2rem;
  color: #1F2937;
  margin-bottom: 1.5rem;
}

.signUpIcon {
  width: 80px;
  height: 80px;
  margin: 0 auto 1.5rem;
  background-image: url('https://cdn-icons-png.flaticon.com/512/9897/9897273.png');
  background-size: cover;
  background-position: center;
}

.signUpIcon img {
  display: none;
}

.formInputLabel {
  font-weight: 500;
  text-align: left;
  display: block;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  color: #212529;
}

.signUpInput {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 1px solid #D1D5DB;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.signUpInput:focus {
  outline: none;
  border-color: #29B6F6;
  box-shadow: 0 0 0 3px rgba(41, 182, 246, 0.2);
}

.signUpBtn {
  width: 100%;
  padding: 0.75rem;
  margin-top: 2rem;
  border-radius: 8px;
  background-color: #FFA500;
  border: none;
  color: white;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.signUpBtn:hover {
  background-color: #4FC3F7;
  transform: translateY(-2px);
}