.card {
  background: #7b8fd633;
  border-radius: 8px;
  box-shadow: 0px 2px 6px rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 12px;
  text-align: center;
  transition: transform 0.2s;
}

.card:hover {
  transform: translateY(-4px);
}

.cardImg {
  width: 100%;
  height: 160px;
  object-fit: cover;
  border-radius: 6px;
  margin-bottom: 10px;
}

.cardInfo{
  width: 80%;
}

.cardInfo h3 {
  font-size: 18px;
  margin-bottom: 5px;
}

.cardInfo p {
  font-size: 14px;
  margin-bottom: 6px;
}

.purchaseBtn {
  padding: 8px 12px;
  background: #20C997;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
}

.purchaseBtn:hover {
  background: #1ABC9C;
}

.disableBtn{
  padding: 8px 12px;
  background: #6c757d;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: not-allowed;
  font-weight: bold;
}