.homePage {
  margin-top: 5vh; 
  padding: 2%;
  height: 100vh;
}


.historyBtn{
  background-color: #4caf4ff3; 
  color: white; 
  padding: 10px 20px;
  margin: 10px 0;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  transition: all 0.3s ease;
}

.historyBtn:hover {
  background-color: #45a049; 
  transform: scale(1.05); 
}

.historyBtn:active {
  background-color: #3e8e41; 
  transform: scale(0.98); 
}

.historyBtn:focus {
  outline: none;
  box-shadow: 0 0 5px rgba(72, 239, 128, 0.7); 
}
