.adminPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px;
  font-family: Arial, sans-serif;
}

.adminCard {
  background: #0774e128;
  padding: 20px 30px;
  border-radius: 10px;
  width: 400px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.12);
  margin-bottom: 40px;
}

.pageHead {
  text-align: center;
  margin-bottom: 20px;
  font-size: 22px;
}

.formInputLabel {
  margin-top: 12px;
  margin-bottom: 5px;
  font-size: 14px;
  font-weight: bold;
}

.adminInput {
  width: 100%;
  padding: 8px 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  margin-bottom: 8px;
}

.adminBtn {
  width: 100%;
  background: #007bff;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  padding: 10px;
  margin-top: 10px;
  cursor: pointer;
}

.adminBtn:hover {
  background: #0056b3;
}

.productList {
  width: 100%;
  max-width: 800px;
}

.listHead {
  margin-bottom: 15px;
}

.emptyText {
  text-align: center;
  color: #777;
}

.productCard {
  display: flex;
  align-items: center;
  background: #e8eaebec;
  border-radius: 8px;
  box-shadow: 0px 2px 6px rgba(0,0,0,0.1);
  margin-bottom: 15px;
  padding: 12px;
  justify-content: space-between;
}

.productImg {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 6px;
  margin-right: 15px;
}

.productInfo {
  flex: 1;
}

.deleteBtn {
  background: #dc3545;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 6px;
  cursor: pointer;
}

.deleteBtn:hover {
  background: #a71d2a;
}
