.historySection {
  background: #f9f9f9;
  border-radius: 12px;
  padding: 1.5rem;
  margin-top: 2rem;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  max-height: 80vh;
  overflow-y: auto;
}

.historyHead {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: #333;
  border-bottom: 2px solid #ddd;
  padding-bottom: 0.5rem;
}

.emptyText {
  color: #888;
  font-size: 1rem;
  text-align: center;
  padding: 1rem 0;
}

.historyList {
  list-style: none;
  margin: 0;
  padding: 0;
}

.historyItem {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  padding: 0.8rem 0;
  border-bottom: 1px solid #e2e2e2;
  transition: background 0.2s ease;
}

.historyItem:hover {
  background: #f0f0f0;
  border-radius: 6px;
}

.historyImg {
  width: 60px;
  height: 60px;
  border-radius: 8px;
  object-fit: cover;
  border: 1px solid #ddd;
}

.historyItem p {
  margin: 2px 0;
  font-size: 0.9rem;
  color: #444;
}

.historyItem strong {
  font-size: 1rem;
  color: #222;
}

.historyItem small {
  font-size: 0.8rem;
  color: #777;
}
