.navbar{
    background-color: #1E90FF;
    display:flex;
    justify-content: space-around;
    align-items: center;
    padding: 0.5%;
    position: fixed;
    top: 0;
    width: 100%;
    height: 5%;
    margin-bottom:7%;
}

.navHead{
    background-color: transparent;
    border: none;
    cursor: pointer;
    width: 20%;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: bold;
    font-size: xx-large;
    color: #FFD700;
}

.navLinks{
    width: 40%;
    display:flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: large;
    font-weight: bold;
}

.navItems{
    text-decoration: none;
    color: #FFA500;
}

.profileName{
    background-color: transparent;
    border: none;
    cursor: pointer;
    width: 7%;
    height: 100%;
    font-size: medium;
    font-style: italic;
    display: flex;
    align-items: center;
    justify-content: space-around;
}



/* MEDIA QUERIES */
@media (max-width: 768px) {

  .navbar{
    margin-bottom: 15% ;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  
  .navHead{
    width: 70%;
  }

  .hamburger {
    width: 10%;
    display: block;
    font-size: 2rem;
    background: none;
    border: none;
    color: red;
    cursor: pointer;
    /* margin-right: 2%; */
  }

  .navLinks {
    display: none;
    flex-direction: column;
    align-items: center;
    background-color: #EAC8A6;
    border-bottom: 1px solid #ccc;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    z-index: 1000;
    padding: 1em 0;
  }

  .show {
    display: flex;
  }

  .navItems {
    margin: 0.5em 0;
    font-size: 1.2em;
  }
}

@media screen and (min-width: 1025px) {
  .hamburger {
    display: none ;
  }

  .navLinks {
    display: flex !important;
    flex-direction: row;
    justify-content: center;
    gap: 2em;
    position: static;
    width: auto;
    background: none;
  }
}
